	export default {
        // 声明要接收的父组件传递过来的属性
        props: {
            
        },
        
        data (){
            return {
                currentTab: 0
            }
        },
        created() {
            this.currentTab = this.globalData.currentTab;
        },
        methods: {
            // 切换页面
            switchPage(e){
                var index = e.currentTarget.dataset.index;
                this.globalData.currentTab = index;
                if (index == 0){
                    uni.switchTab({
                        url: '/pages/index/index',
                    })
                } else if(index == 1){
                    uni.switchTab({
                        url: '/pages/community/index',
                    })
                } else if(index == 2){
                    
                } else if(index == 3){
                    uni.switchTab({
                        url: '/pages/news/index',
                    })
                } else if(index == 4){
                    uni.switchTab({
                        url: '/pages/personal/index',
                    })
                }
            }
        },
	}
